// Labels

.label {
  border-radius: 0;
  font-weight: normal;
  color: $grey-base;
}

.label--default {
  @extend .label-default;
  color: $label_text_color_light;
}

.label--primary {
  @extend .label-primary;
  color: $label_text_color_light;
}

.label--success {
  @extend .label-success;
  color: $label_text_color_light;
}

.label--info {
  @extend .label-info;
  color: $label_text_color_light;
}

.label--warning {
  @extend .label-warning;
  color: $label_text_color_light;
}

.label--danger {
  @extend .label-danger;
  color: $label_text_color_light;
}

.label--upper {
  text-transform: uppercase;
  text-align: left;
  font-size: 0.8em;
}

.label--highlight {
  color: $grey-base;
  background-color: $yellow-light;
  &.label--meta {
    &:after {
      content: '';
      margin: 0;
    }
  }
}

.label--meta {
  @extend .meta__item;
  padding: 0.25em 0.5em 0.3em 0.5em;
  font-size: 90%;
  top: -0.075em;
  margin-right: 0.8em;
  position: relative;
}
